.single__main {
  background-color: $color_base;

  .single__header {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 32rem;
    color: #ccc;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.8);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    background-blend-mode: darken;

    img {
      width: 100%;
      height: auto;
    }

    .single__title {
      padding: 0 1em;
      font-size: 3.6rem;
      font-weight: bold;
    }

    .single__meta {
      padding: 2.2rem;
      font-size: 1.2rem;

      a {
        display: inline-block;
        padding: 0 1em;
      }

      time {
        display: inline-block;
        padding-top: 1em;
      }
    }
  }

  .single__content {
    max-width: 94rem;
    padding: 3.3rem 2rem;
    margin: auto;
    color: #333;

    h2 {
      padding: 5.5rem 0 0.5em 0.5em;
      font-size: 2.4rem;
      border-bottom: 1px solid #ccc;
    }

    h3 {
      padding-top: 3.3rem;
      padding-left: 0.5em;
      font-size: 2rem;
    }

    p {
      padding: 1em;
    }

    a {
      font-weight: bold;
      color: #16a34a;
      text-decoration: underline;
    }

    #toc_container {
      width: 100% !important;
      margin: 3.3rem auto;

      .toc_title {
        text-align: left;
      }
    }

    .sns-list {
      display: flex;
      flex-wrap: wrap;
      padding-top: 11rem;

      span {
        width: 100%;
        padding-left: 1em;
      }

      li {
        padding-top: 1em;
        padding-left: 1em;

        .sns-link {

        }

        img {

        }
      }
    }
  }
}

.single__breadcrumb-wrapper {
  background-color: $color_base;

  .breadcrumb {
    max-width: 90rem;
    margin: auto;
  }
}
